import React, { useState } from "react";
import {
  Box,
  Button,
  FormControl,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
} from "@material-ui/core";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { useLocation } from "react-router-dom";
import { Alert, AlertTitle } from "@material-ui/lab";
import axios from "axios";
import appConstants from "../Constants/AppContants";

// A custom hook that builds on useLocation to parse
// the query string for you.
function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function ResetPassword() {
  const query = useQuery();

  const [formData, setFormData] = useState({
    code: query.get("code"),
    password: "",
    passwordConfirmation: "",
    showPassword: false,
  });

  const [isLoading, setIsLoading] = useState(false);
  const [formSuccess, setFormSuccess] = useState(false);
  const [showError, setShowError] = useState({
    showError: false,
    errorMessage: "",
  });

  const onInputChange = (e) => {
    const { target } = e;
    setFormData({
      ...formData,
      [target.id]: target.value,
    });
  };

  const handleClickShowPassword = () => {
    setFormData({ ...formData, showPassword: !formData.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const postRequest = async () => {
    try {
      const response = await axios.post(
        appConstants.resetPasswordAPI,
        formData
      );

      // If success, show success message
      if (response.status === 200) {
        setFormSuccess(true);
      } else {
        // on request error
        setShowError({
          showError: true,
          errorMessage: "An error ocurred",
        });
      }
    } catch (err) {
      const errorMessage =
        err?.response?.data?.data[0]?.messages[0]?.id ===
        "Auth.form.error.code.provide"
          ? "Code EXPIRED or it's INCORRECT."
          : err?.response?.data?.data[0]?.messages[0]?.message;

      setShowError({
        showError: true,
        errorMessage: errorMessage || "An error ocurred",
      });

      setIsLoading(false);
    }
  };

  const submitForm = (event) => {
    event.preventDefault();

    if (formData.code) {
      setIsLoading(true);
      setShowError({ showError: false });
      postRequest();
    } else {
      setShowError({
        showError: true,
        errorMessage: "There is not code provided.",
      });
    }
  };

  return (
    <div className="myappets__reset-pass reset-pass">
      <h2>My Appets, Reset Password</h2>
      <div className="reset-pass__cont">
        {!formSuccess && (
          <form onSubmit={(e) => submitForm(e)} noValidate autoComplete="off">
            <Box py={1}>
              <FormControl fullWidth>
                <InputLabel htmlFor="password">
                  Password: (min-length: 6 characters)
                </InputLabel>
                <Input
                  id="password"
                  type={formData.showPassword ? "text" : "password"}
                  onChange={(e) => onInputChange(e)}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {formData.showPassword ? (
                          <Visibility />
                        ) : (
                          <VisibilityOff />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            </Box>
            <Box py={1}>
              <FormControl fullWidth>
                <InputLabel htmlFor="passwordConfirmation">
                  Confirm Password:
                </InputLabel>
                <Input
                  id="passwordConfirmation"
                  type={formData.showPassword ? "text" : "password"}
                  onChange={(e) => onInputChange(e)}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {formData.showPassword ? (
                          <Visibility />
                        ) : (
                          <VisibilityOff />
                        )}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            </Box>

            <Box py={1}>
              <Button
                type="submit"
                variant="contained"
                color="secondary"
                disabled={
                  isLoading ||
                  !formData.password ||
                  !formData.passwordConfirmation ||
                  formData.password !== formData.passwordConfirmation ||
                  formData.password.length < 6 ||
                  formData.passwordConfirmation.length < 6
                }
              >
                {!isLoading ? "Reset Password" : "Loading"}
              </Button>
            </Box>
            {showError.showError && (
              <Alert severity="error">{showError.errorMessage}</Alert>
            )}
          </form>
        )}
        {formSuccess && (
          <Alert severity="success">
            <AlertTitle>
              Your password was changed <strong>correctly</strong>
            </AlertTitle>
            Now you can log in in the app with your new password
          </Alert>
        )}
      </div>
    </div>
  );
}

export default ResetPassword;
